import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import CountUp from 'react-countup';
import './Offer.css'
import Wow from 'wow.js';

import medical_plan_doc from './assets/current_medical.pdf';
import dental_plan_doc from './assets/current_dental.pdf';
import vision_plan_doc from './assets/current_vision.pdf';
import life_disability_plan_doc from './assets/current_life_disability.pdf';


const retirement_plan = 'https://www.guideline.com/investments'


function Breakdown(props) {
    // Create our number formatter.
    const formatter = new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'USD',
    });

    return <div className="wow bounceIn">
        <h4 style={{marginTop: 50, marginBottom: 30 }}>Here's how it all breaks down...</h4>
        <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'space-around' }}>
            {
                [
                    { description: `Hourly Rate`, value: props.rate, icon: 'dollar-sign' },
                    { description: `Regular Wages${ props.bonus ? ' + Signing Bonus' : ''}`, value: props.wages, icon: 'dollar-sign' },
                    { description: 'Earned PTO', value: props.ptoContribution, icon: 'umbrella-beach' },
                    { description: '401k Contribution', value: props.retirementContribution, icon: 'piggy-bank', plan: retirement_plan },
                    { description: 'HSA Contribution', value: props.hsaContribution, icon: 'credit-card' },
                    { description: 'Medical Insurance', value: props.medicalContribution, icon: 'hospital', plan: medical_plan_doc },
                    { description: 'Dental Insurance', value: props.dentalContribution, icon: 'tooth', plan: dental_plan_doc },
                    { description: 'Vision Insurance', value: props.visionContribution, icon: 'glasses', plan: vision_plan_doc },
                    { description: 'Disability and Life Insurance', value: props.lifeAndDisabilityContribution, icon: 'crutch', plan: life_disability_plan_doc },

                ].filter(x => x.value).map(({ description, value, icon, plan }, i) => (
                    <div key={description} className="wow bounceIn" style={{ flex: `0 ${window.innerWidth > 1024 ? '33' : '50'}%`, height: 100, margin: '20px 0 80px', animationDelay: `${1 + i*0.1}s`}}>
                        <i style={{marginBottom: 15}} className={`fas fa-2x fa-${icon}`} />
                        <h6>{description}</h6>
                        <h5 style={{ marginBottom: plan ? 2 : undefined }}>{formatter.format(value)}</h5>
                        { 
                          plan && (
                            <a href={plan} target="__BLANK">Plan Summary</a>
                          )
                        }
                    </div>
                ))
            }
        </div>
    </div>
}


export default function Offer() {

    const [settingsChanged, setSettingsChanged] = useState(false);
  
    useEffect(() => {
        new Wow().init();
    })


    const offerCode = useSelector(x => x.offerCode)
    const offer = useSelector(x => x.offer)
    const baseRate = offer.rate
    const signingBonus = offer.signingBonus
    const hasInsurance = offer.hasInsurance
    const title = offer.title

    const { 
        hours, 
        waivedInsuranceRateAdjustment,
        ptoAccrualRate,
        retirementContributionRate, 
        lifeAndDisabilityContribution, 
        ...optionalContributions
    } = useSelector(x => x.settings);
    
    const dispatch = useDispatch();

    // calculate income
    const rate = hasInsurance ? baseRate : baseRate + waivedInsuranceRateAdjustment;
    const wages = rate * hours;
    const bonus = signingBonus 
    const ptoHours = ptoAccrualRate * hours
    const pto = rate * ptoHours;
    const income = wages + pto;
    const retirement = income * retirementContributionRate
    
    // default weeks of vacation
    let vacationWeeks = 52 - hours/40
    
    const medicalContribution = hasInsurance ? optionalContributions.medicalContribution : 0;
    const hsaContribution = hasInsurance ? optionalContributions.hsaContribution : 0;
    const dentalContribution = hasInsurance ? optionalContributions.dentalContribution : 0;
    const visionContribution = hasInsurance ? optionalContributions.visionContribution : 0;
    const totalInsuranceContribution = medicalContribution + dentalContribution + visionContribution + lifeAndDisabilityContribution;
    const totalCompensation = income + bonus + retirement + totalInsuranceContribution;
    
    return (
        <div className="Offer text-center" key={offerCode}>
          <div className="Offer-Summary">
            <section className={!settingsChanged ? "wow fadeInDown" : ''}>
              <h4 style={{ marginBottom: 10 }}>We are pleased to offer you the position of</h4>
              <h1>{title}</h1>
     
            </section>
            <section className={!settingsChanged ? "wow fadeInDown" : ''}>
              <h4>...with a projected <strong>annual income</strong> of...</h4>
              <h1>
                { hours && <span><CountUp delay={0.5} duration={1} end={income} separator="," prefix="$" decimals={2} />/year</span> }
                { !hours && '-' }
              </h1>
             { 
                signingBonus > 0 && [
                  <h5 key="signingBonusLead">...and a</h5>, 
                  <h1 key="signingBonus" style={{ fontSize: '3em'}}>
                    <CountUp duration={1} delay={1} end={signingBonus} separator="," prefix="$" decimals={2} /> Signing Bonus
                  </h1>
                ] 
              }
              
              <div style={{ background: 'rgba(255,255,255,0.6)', borderRadius: 20, padding: 30, margin: '60px -5px' }}>
                <h4>We provide a flexible hourly model and believe <strong>YOU</strong> should decide your work/life balance.</h4>
                <br />
                <h5><strong>Plan to work more/less than the standard 1,880 hour year?</strong></h5>
                <br />
                <h5>                  
                  Adjust your time off for a more accurate calculation:
                  <br />
                  <input 
                    style={{ width: '6ex' }}
                    type="number" 
                    min="0"
                    max="26"
                    value={hours === null ? '' : vacationWeeks} 
                    onChange={e => {
                      dispatch({
                        type: 'SETTINGS_CHANGED', 
                        settings: { 
                          hours: e.currentTarget.value === '' ? null : 2080 - Number(e.currentTarget.value) * 40 } 
                      });
                      setSettingsChanged(true);
                    }} 
                  />
                  &nbsp;week(s)
                </h5>
              </div>
            </section>
            <section className={!settingsChanged ? "wow fadeInDown" : ''}>
              <h3>...and did we mention our <strong>20% 401(k) contribution</strong> yet?</h3>
              <br/>
              <h5>For the above estimated income, we would have contributed</h5>
              <h1>
                { hours && <CountUp delay={1} duration={1} end={retirement} separator="," prefix="$" decimals={2} /> }
                { !hours && '-' }

              </h1>
              <h5>towards your retirement!</h5> 
            </section>

            <section className={!settingsChanged ? "wow fadeInDown" : ''}>
              <h3>All together, your projected <strong>total compensation</strong> is...</h3>
              <h1>
                { hours && <CountUp delay={2} duration={1} end={totalCompensation} separator="," prefix="$" decimals={2} />}
                { !hours && '-' }

              </h1>
              {hours && (
                <Breakdown 
                    rate={rate}
                    wages={wages}
                    bonus={bonus} 
                    ptoContribution={pto}
                    medicalContribution={medicalContribution}
                    retirementContribution={retirement} 
                    hsaContribution={hsaContribution}
                    dentalContribution={dentalContribution} 
                    visionContribution={visionContribution}
                    lifeAndDisabilityContribution={lifeAndDisabilityContribution}
                />
              )}
            </section>
          </div>
        </div>
    )
}